import React from "react";
import { BaseComponent, View, Controller } from "../../components";
import {
  Button,
  Input,
  Label,
  InputGroup,
  InputGroupAddon,
  Alert,
} from "reactstrap";
class App extends BaseComponent {
  constructor(props) {
    super(props);
    this.state = { isAlertOpen: true };
  }

  render() {
    const isAlertOpen = this.state.isAlertOpen;
    return (
      <React.Fragment>
        <div
          style={{
            zIndex: 1000,
            position: "fixed",
            left: "50%",
            top: "50%",
            transform: "translate(-50%,-50%)",
          }}
        >
          <Alert
            color="success"
            isOpen={isAlertOpen}
            toggle={() => this.setState({ isAlertOpen: false })}
            style={{ margin: "auto" }}
          >
            <h4 className="alert-heading">Tips!</h4>
            <hr />
            <ul>
              <li>
                To change random Points Number and steiner Points Number by
                typing the RPN (20~1000) and SPN (20~1000).
              </li>
              <li>
                To zoom in the graph by scrolling down the wheel of your mouse,
                similarly for the zooming out.
              </li>
              <li>
                To forward and playback the animation by dragging the progress
                bar anywhere you want.
              </li>
            </ul>
            <p></p>

            <p className="mb-0">
              {" "}
              <b>Have fun :)</b>{" "}
              <button
                style={{ position: "absolute", right: 20, bottom: 10 }}
                onClick={() => this.setState({ isAlertOpen: false })}
                type="button"
                class="btn btn-outline-success"
              >
                Got it !
              </button>
            </p>
          </Alert>
        </div>
        <div
          id="app"
          style={{
            height: "100%",
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
            position: "relative",
            zIndex: 10,
            overflow: "hidden",
          }}
        >
          <div
            style={{
              height: "80%",
              width: "95%",
              backgroundColor: "rgb(3,54,73)",
              alignItems: "center",
              boxShadow:
                "0 0px 0px 0 rgba(0, 0, 0, 0.92),0 4px 8px 0 rgba(0, 0, 0, 0.92)",
            }}
          >
            <View />
          </div>
          <div
            style={{
              height: "10%",
              width: "95%",
              backgroundColor: "rgb(3,101,100)",
              boxShadow:
                "0 4px 8px 0 rgba(0, 0, 0, 0.52),0 4px 8px 0 rgba(0, 0, 0, 0.52)",
            }}
          >
            <Controller />
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default App;



