import React from "react";
class ProgressBar extends React.Component {
  constructor(props) {
    super(props);

    this.handleMouseDown = this.handleMouseDown.bind(this);
    this.handleMouseMove = this.handleMouseMove.bind(this);
    this.handleMouseUp = this.handleMouseUp.bind(this);
    this.offsetWidth = null;
  }

  handleMouseDown(e) {
    this.target = e.target;
    this.handleMouseMove(e);
    console.log(1);
    document.addEventListener("mousemove", this.handleMouseMove);
    document.addEventListener("mouseup", this.handleMouseUp);
    if (e.target.id == "ProgressBar") e.stopPropagation();
  }

  handleMouseMove(e) {
    const { left } = this.target.getBoundingClientRect();
    const { offsetWidth } = this.target;
    if (!this.offsetWidth) {
      this.offsetWidth = offsetWidth;
    }
    const { onChangeProgress } = this.props;
    const progress = (e.clientX - left) / this.offsetWidth;
    if (onChangeProgress) onChangeProgress(progress);
  }
  // 0066CC
  handleMouseUp(e) {
    document.removeEventListener("mousemove", this.handleMouseMove);
    document.removeEventListener("mouseup", this.handleMouseUp);
  }

  render() {
    const { className, total, current } = this.props;

    return (
      <div
        id="ProgressBar"
        style={{
          position: "relative",
          cursor: "pointer",
          pointerEvents: "auto",
          marginLeft: "1%",
          cursor: "pointer",
          height: 34,
          width: "calc(30rem + 4px)",
          backgroundColor: "#99CCCC",
          border: "2px  solid",
          borderImageSlice: 1,

          borderImageSource: "linear-gradient(90deg, #2196f3  0, #ff1744 80%)",
        }}
        onMouseDown={this.handleMouseDown}
      >
        <div
          style={{
            position: "absolute",
            height: 30,
            background: "linear-gradient(90deg, #2196f3  3rem, #ff1744 30rem)",
            width: `${(current / total) * 100}%`,
          }}
        >
          <div
            style={{
              height: 38,
              width: 2,
              backgroundColor: "white",
              // position: "absolute",
              // right: 0,
              float: "right",
              // marginRight: -2,
              marginTop: -4,
            }}
          ></div>
        </div>
      </div>
    );
  }
}

export default ProgressBar;



