import React from "react";
import { connect } from "react-redux";
import { BaseComponent } from "../../components";
import { actions } from "../../reducers";
import { Tree } from "../../core/";
import { test } from "./code.js";

class View extends BaseComponent {
  constructor(props) {
    super(props);
    this.randomPointsNumber = null;
    this.steinerPointsNumber = null;
    this.operations = [];
    this.root = null;
    this.oldProgress = -1;
    this.objects = {};
    this.set(
      this.props.player.randomPointsNumber,
      this.props.player.steinerPointsNumber
    );
    this.state = { replay: false };
  }

  set(randomPointsNumber, steinerPointsNumber) {
    this.randomPointsNumber = randomPointsNumber;
    this.steinerPointsNumber = steinerPointsNumber;
    this.operations = test(this.randomPointsNumber, this.steinerPointsNumber);
    this.props.setTotal(this.operations.length);
  }
  componentDidUpdate(prevProps) {
    const oldProgress = prevProps.player.progress;
    var newProgress = this.props.player.progress;
    const { randomPointsNumber, steinerPointsNumber } = prevProps.player;
    if (
      randomPointsNumber != this.props.player.randomPointsNumber ||
      steinerPointsNumber != this.props.player.steinerPointsNumber
    ) {
      this.set(
        this.props.player.randomPointsNumber,
        this.props.player.steinerPointsNumber
      );
      this.objects["Tree"] = new Tree("Tree", "Tree");
      // this.oldProgress = -1;
    }

    // if (this.oldProgress == -1) {
    //   this.objects["Tree"] = new Tree("Tree", "Tree");
    // }
    if (oldProgress + 1 <= newProgress) {
      this.applyOperations(
        this.operations.slice(oldProgress + 1, newProgress + 1)
      );
    } else {
      // if (newProgress != this.props.player.total) {
      this.objects["Tree"] = new Tree("Tree", "Tree");
      this.applyOperations(this.operations.slice(0, newProgress + 1));
      // }
    }
  }
  // componentWillReceiveProps(nextProps) {}

  applyOperations(operations) {
    operations.forEach((steps) => this.applySteps(steps));
  }

  applyCommand(command) {
    const { methodName, args } = command;

    this.objects["Tree"][methodName](...args);
  }

  applySteps(steps) {
    steps.forEach((command) => this.applyCommand(command));
  }

  render() {
    return (
      <div
        id="View"
        style={{
          position: "relative",
          height: "100%",
          width: "100%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div
          style={{ position: "absolute", bottom: 0, right: 0, color: "white" }}
        >
          <i class="fa fa-search-plus" aria-hidden="true"></i>
          100%
        </div>
        {[this.objects["Tree"]].map((tracer) => tracer && tracer.render())}
      </div>
    );
  }
}

export default connect(({ player }) => ({ player }), actions)(View);



