import { combineActions, createAction, handleActions } from 'redux-actions';

const setRandomPointsNumber = createAction(`setRandomPointsNumber`, (randomPointsNumber) => {
  return {randomPointsNumber};
});

const setSteinerPointsNumber = createAction(`steinerPointsNumber`, (steinerPointsNumber) => {
  return {steinerPointsNumber};
});

const setPlaying = createAction(`setPlaying`, (playing) => {
  return {playing};
});

const setTotal = createAction(`setTotal`, total => ({ total }));

const setProgress = createAction(`setProgress`, progress => ({ progress }));

export const actions = {
  setTotal,
  setPlaying,
  setProgress,
  setRandomPointsNumber,
  setSteinerPointsNumber,
};

const defaultState = {
  progress: 0,
  total:1,
  randomPointsNumber:50,
  steinerPointsNumber:50,
};

export default handleActions({
  [combineActions(
    setTotal,
    setPlaying,
    setProgress,
    setRandomPointsNumber,
    setSteinerPointsNumber
  )]: (state, { payload }) => ({
    ...state,
    ...payload,
  }),
}, defaultState);



