import { TreeRender } from '../core/';
import { Render } from '../core/';
import React from 'react';
class Tree {

  constructor(key, getObject, title) {
    this.key = key;
    this.getObject = getObject;
    this.init();
    this.set();
  }

  getRendererClass() {
    return TreeRender;
  }

  init() {
    this.dimensions = {
      width: 520,
      height: 520,
    };
    this.callLayout = { method: this.layoutRect, args: [] };
  }

  set(array2d = []) {
    this.nodes = [];
    this.edges = [];
    this.layout();
  }

  addNode(id, x = 0, y = 0, visitedCount = 0) {
    this.nodes.push({ id, x, y, visitedCount});
    this.layout();
  }

  removeNode(id) {
    const node = this.findNode(id);
    if (!node) return;
    const index = this.nodes.indexOf(node);
    this.nodes.splice(index, 1);
    this.layout();
  }

  addEdge(source, target, visitedCount = 0) {
    if (this.findEdge(source, target)) return;
    this.edges.push({ source, target, visitedCount});
    this.layout();
  }

  removeEdge(source, target) {
    const edge = this.findEdge(source, target);
    if (!edge) return;
    const index = this.edges.indexOf(edge);
    this.edges.splice(index, 1);
    this.layout();
  }

  findNode(id) {
    return this.nodes.find(node => node.id === id);
  }

  findEdge(source, target) {
      return this.edges.find(edge =>
        edge.source === source && edge.target === target ||
        edge.source === target && edge.target === source);
  }

  getRect() {
    const { width, height} = this.dimensions;
    const left = -width / 2 ;
    const top = -height / 2 ;
    const right = width / 2 ;
    const bottom = height / 2;
    return { left, top, right, bottom, width, height };
  }

  layout() {
    const { method, args } = this.callLayout;
    method.apply(this, args);
  }

  layoutRect() {
    const rect = this.getRect();
  }

  visit(target, source) {
    this.visitOrLeave(true, target, source);
  }

  leave(target, source) {
    this.visitOrLeave(false, target, source);
  }

  visitOrLeave(visit, target, source = null) {
    const edge = this.findEdge(source, target);
    if (edge) edge.visitedCount += visit ? 1 : -1;
    const node = this.findNode(target);
    node.visitedCount += visit ? 1 : -1;
  }

  render() {
    return (
      <TreeRender key={this.key} data={this} />
    );
  }
}

export default Tree;



