import React from "react";
import { Render } from "../core/";

class TreeRender extends Render {
  constructor(props) {
    super(props);
    this.elementRef = React.createRef();
  }

  renderData() {
    const { nodes, edges, dimensions } = this.props.data;
    const { width, height } = dimensions;
    const viewBox = [(this.centerX - width / 2) * this.zoom, (this.centerY - height / 2) * this.zoom, width * this.zoom, height * this.zoom];
    return (
      <svg style={{ height: "100%", width: "100%" }} fill-rule="evenodd" viewBox={viewBox} ref={this.elementRef}>
        {edges
          .sort((a, b) => a.visitedCount - b.visitedCount)
          .map((edge) => {
            const { source, target, weight, visitedCount } = edge;
            const sourceNode = this.props.data.findNode(source);
            const targetNode = this.props.data.findNode(target);
            const { x: sx, y: sy } = sourceNode;
            let { x: ex, y: ey } = targetNode;
            return (
              <g key={`${source}-${target}`} stroke={visitedCount ? "red" : "white"}>
                <path d={`M${sx},${-sy} L${ex},${-ey}`} strokeWidth={4} />
              </g>
            );
          })}
        {nodes.map((node) => {
          const { id, x, y, weight, visitedCount } = node;
          return (
            <g key={id} transform={`translate(${x},${-y})`}>
              <circle fill={visitedCount ? "red" : "white"} r={4} />
            </g>
          );
        })}
      </svg>
    );
  }
}
export default TreeRender;



