import React from "react";
import { connect } from "react-redux";
import { actions } from "../../reducers";
import { BaseComponent, ProgressBar } from "../../components";
import { Button, Input, Label, InputGroup, InputGroupAddon } from "reactstrap";
class Controller extends BaseComponent {
  constructor(props) {
    super(props);

    this.playing = false;
    this.progressNumber = 0;
    this.total = this.props.player.total;
    this.changeRandomPointsNumber = this.changeRandomPointsNumber.bind(this);
    this.changeSteinerPointsNumber = this.changeSteinerPointsNumber.bind(this);
    this.set = this.set.bind(this);
    this.state = { crpn: 50, spn: 50 };
  }

  componentWillReceiveProps(nextProps) {
    if (this.total != this.props.player.total) {
      this.total = this.props.player.total;
      this.progressNumber = 0;
    }
  }

  applyChunk(index) {
    this.progressNumber = index;
    if (!this.playing) return;
    if (this.timer) {
      window.clearTimeout(this.timer);
      this.timer = undefined;
    }
    if (index < this.total) {
      this.timer = window.setTimeout(() => {
        index++;
        this.applyChunk(index);
      }, 16.7);
      this.props.setProgress(index);
    } else {
      this.playing = false;
      this.props.setPlaying(this.playing);
    }
  }

  build() {
    this.playing = !this.playing;
    this.props.setPlaying(this.playing);
    if (this.progressNumber == this.total) {
      this.progressNumber = 0;
      window.setTimeout(() => {
        this.applyChunk(this.progressNumber);
      }, 206.7);
    } else {
      this.applyChunk(this.progressNumber);
    }
  }

  onChangeProgress(progress) {
    if (this.timer) {
      window.clearTimeout(this.timer);
      this.timer = undefined;
    }
    if (this.playing) {
      new Promise((resolve) => {
        window.setTimeout(() => {
          this.playing = false;
          this.props.setPlaying(this.playing);
          resolve();
        }, 16.7);
      })
        .then(() => {
          var progressNumber = Math.round(progress * this.total);
          this.progressNumber =
            progressNumber < this.total ? progressNumber : this.total;
          // console.log(progressNumber);
          this.props.setProgress(this.progressNumber);
        })
        .then(() => {
          this.build();
        });
    } else {
      new Promise((resolve) => {
        var progressNumber = Math.round(progress * this.total);
        this.progressNumber =
          progressNumber < this.total ? progressNumber : this.total;
        // console.log(progressNumber);
        this.props.setProgress(this.progressNumber);
        resolve();
      }).then(() => {
        this.build();
      });
    }
  }

  changeRandomPointsNumber(e) {
    let crpn = e.target.value >= 0 ? e.target.value : 3;
    crpn = crpn <= 1000 ? crpn : 1000;

    this.setState({ crpn });
  }

  changeSteinerPointsNumber(e) {
    let spn = e.target.value >= 0 ? e.target.value : 3;
    spn = spn <= 1000 ? spn : 1000;

    this.setState({ spn });
  }

  set() {
    this.playing = false;
    new Promise((resolve) => {
      window.setTimeout(() => {
        this.props.setPlaying(false);
        resolve();
      }, 16.7);
    }).then(() => {
      if (this.state.crpn >= 3)
        this.props.setRandomPointsNumber(this.state.crpn);
      else {
        this.props.setRandomPointsNumber(3);
        this.setState({ crpn: 3 });
      }

      if (this.state.spn >= 3)
        this.props.setSteinerPointsNumber(this.state.spn);
      else {
        this.props.setSteinerPointsNumber(3);
        this.setState({ spn: 3 });
      }

      this.progressNumber = 0;
      this.props.setProgress(0);
      this.build();
    });
  }

  render() {
    const { className } = this.props;
    const { crpn, spn } = this.state;
    return (
      <div
        id="Controller"
        style={{
          height: "100%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div
          style={{
            height: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            padding: "0 1rem",
          }}
        >
          <InputGroupAddon addonType="prepend">
            <Button
              type="button"
              class="none-border"
              color="success"
              onClick={() => this.set()}
            >
              <span class="glyphicon glyphicon-repeat"> Set</span>
            </Button>
          </InputGroupAddon>
          <div style={{ marginLeft: "2%", width: 45 }}>
            <font size="4" color="white" face="Calibri">
              RPN:
            </font>
          </div>
          <Input
            type="number"
            value={crpn}
            onChange={this.changeRandomPointsNumber}
            style={{ maxWidth: "5rem", backgroundColor: "#99CCCC" }}
          />
          <div style={{ marginLeft: "2%", width: 45 }}>
            <font size="4" color="white" face="Calibri">
              SPN:
            </font>
          </div>
          <Input
            type="number"
            max={500}
            value={spn}
            onChange={this.changeSteinerPointsNumber}
            style={{ maxWidth: "5rem", backgroundColor: "#99CCCC" }}
          />
        </div>

        <Button
          type="button"
          class="none-border"
          color="success"
          onClick={() => this.build()}
          style={{ marginLeft: "5rem", alignItems: "center", width: "5rem" }}
        >
          {this.playing ? (
            <i class="fas fa-pause"></i>
          ) : (
            <i class="fas fa-play"> </i>
          )}
        </Button>

        <ProgressBar
          current={this.progressNumber + 1}
          total={this.total}
          onChangeProgress={(progress) => this.onChangeProgress(progress)}
        />
        <div style={{ height: 24, padding: "0 10px" }}>
          <font size="4" color="white" face="Arial">
            {this.progressNumber}/{this.total}
          </font>
        </div>
      </div>
    );
  }
}

export default connect(({ player }) => ({ player }), actions)(Controller);



